.header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 56px;
	padding: 0;
	background: #673AB7;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
	z-index: 999;
}

.header h1 {
	float: left;
	margin: 0;
	padding: 0 15px;
	font-size: 24px;
	line-height: 56px;
	font-weight: 400;
	color: #FFF;
}

.header nav {
	float: right;
	font-size: 100%;
	display: flex;
	flex-direction: column;
	position: relative;

	background: #673AB7;
	border-radius: 0px 0px 0px 10px;
	overflow: hidden;
	z-index: 999;

	top: 56px;
}

.header nav a {
	display: inline-block;
	height: 56px;
	line-height: 56px;
	padding: 0 15px;
	min-width: 50px;
	text-align: right;
	background: rgba(255,255,255,0);
	text-decoration: none;
	color: #FFF;
	will-change: background-color;
}

.header nav a:hover,
.header nav a:active {
	background: rgba(0,0,0,0.2);
}

.header nav a.active {
	background: rgba(0,0,0,0.4);
}

.btn {
	height: 56px;
	float: right;
	color: white;
	position: absolute;
	right: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 15px;
	font-size: 1.8em;
}

.hide {
	display: none !important;
}